.Header {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    margin-bottom: 0.5rem;
    margin-top: 0.25rem;
}

.Header:hover {
    cursor: default;
}

.img-text-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-left: 1rem;
    gap: 0.5em;
}

.lineImage {
    height: 2rem;
    width: auto;
}

h1 {
    margin-bottom: 0.25em;
}

p {
    width: fit-content !important;
}
